
body {
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 17px;
  color: #515151;
  box-sizing: border-box;
}
.main-wrapper {
  max-width: 1300px;
  margin: 0 auto;
}
.wrapper {
  max-width: 980px;
  margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5,
p,
li,
ul {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 21px 140px;
  align-items: flex-end;
}
.header-nav {
  padding-bottom: 3px;
}
.header-nav a {
  color: #666666;
  font-size: 13px;
  font-weight: 600;
  line-height: 1;
  text-transform: uppercase;
  margin-right: 28px;
  transition: 0.3s;
}
.header-nav a:hover {
  color: #add136;
}
.burger__menu {
  display: none;
}
.burger__menu span {
  content: '';
  display: block;
  width: 24px;
  height: 3px;
  background-color: #515151;
  margin-bottom: 6px;
}

.intro {
  background: url(../../img/Overlay.png) no-repeat;
  background-size: cover;
  height: 570px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  position: relative;
}
.intro h1 {
  text-align: center;
  font-size: 49px;
  font-weight: 600;
  line-height: 77px;
}
.intro h1 span {
  font-size: 35px;
  display: block;
  font-weight: 400;
}
.intro__btn {
  margin-top: 55px;
  padding: 28px 38px 25px;
  color: #ffffff;
  font-weight: 600;
  font-size: 19px;
  line-height: 1;
  background-color: #99c50a;
  border-radius: 8px;
  transition: 0.1s;
}
.intro__btn:hover {
  box-shadow: 0 0 7px 2px rgba(81, 81, 81, 0.6);
}

.presentation {
  padding: 100px 0 150px;
  text-align: center;
}
.presentation h2 {
  margin: 50px 0;
  text-transform: uppercase;
  font-size: 31px;
  font-weight: 600;
  line-height: 50px;
}
.presentation p {
  margin: 50px 0;
  font-size: 19px;
  color: #515151;
  opacity: 0.7;
}

.work {
  display: flex;
}
.work-properties__wrap {
  display: flex;
  width: 50%;
  padding: 46px 75px 46px 147px;
  background-color: #e2f8ff;
  text-align: right;
}
.work-biers__wrap {
  display: flex;
  width: 50%;
  padding: 46px 147px 46px 75px;
  background-color: #f5f9eb;
}
.work-properties {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.work-properties .work-article h3 {
  font-family: Montserrat;
  font-size: 23px;
  font-weight: 600;
}
.work-properties .work-article p {
  font-size: 17px;
  font-weight: 400;
  line-height: 2;
  margin: 32px 0 51px;
}
.work-properties .work-article a {
  padding: 19px 25px;
  line-height: 1;
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  background-color: #26a6d1;
  border-radius: 10px;
}
.work-properties .work-article a:hover {
  box-shadow: 0 0 5px 1px rgba(81, 81, 81, 0.5);
}
.work-properties .work__pic {
  margin-right: 13px;
}
.work-biers {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.work-biers .work-article h3 {
  font-family: Montserrat;
  font-size: 23px;
  font-weight: 600;
}
.work-biers .work-article p {
  font-size: 17px;
  font-weight: 400;
  line-height: 2;
  margin: 32px 0 51px;
}
.work-biers .work-article a {
  padding: 19px 25px;
  line-height: 1;
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  background-color: #add136;
  border-radius: 10px;
}
.work-biers .work-article a:hover {
  box-shadow: 0 0 5px 1px rgba(81, 81, 81, 0.5);
}
.work-biers .work__pic {
  margin-right: 45px;
}

.projects {
  padding: 80px 0;
}
.projects-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 65px;
}
.projects-heading__article {
  width: 68.8%;
}
.projects-heading__article h2 {
  font-size: 29px;
  font-weight: 600;
}
.projects-heading__article p {
  color: #8f8f8f;
  font-size: 17px;
  font-weight: 400;
  margin-top: 26px;
}
.projects-heading__btn {
  padding: 18px 24px;
  font-weight: 600;
  font-size: 15px;
  opacity: 0.7;
  border: 1px solid #515151;
  border-radius: 10px;
}
.projects-heading__btn:hover {
  color: #fff;
  background-color: #add136;
  opacity: 1;
  box-shadow: 0 0 5px 1px rgba(81, 81, 81, 0.5);
}
.projects-list {
  display: flex;
  justify-content: space-between;
}
.projects-item {
  height: inherit;
  width: 30.6%;
  border: 1px solid rgba(0, 0, 0, 0.14);
  background-color: #f7f7f7;
  border-radius: 10px;
  border-top: none;
}
.projects__img {
  width: calc(100% + 3px);
  position: relative;
  left: -2px;
}
.projects__img img {
  width: 100%;
  height: auto;
}
.projects-item__article {
  padding: 29px 22px 24px;
}
.projects-item__article h3 {
  font-size: 17px;
  font-weight: 700;
  color: #484848;
}
.projects-item__article p {
  font-size: 13px;
  font-weight: 400;
  color: #484848;
  opacity: 0.79;
  margin: 17px 0 20px;
  line-height: 1.8;
}
.projects-progress {
  width: 100%;
  height: 14px;
  background-color: #e3e3e3;
  content: '';
  border-radius: 6px;
  margin-top: auto;
}
.projects-progress span {
  content: '';
  background-color: #add136;
  height: 14px;
  z-index: 1;
  display: block;
  padding: 0;
  margin: 0;
  border-radius: 10px 0 0 10px;
}
.projects-item__date {
  text-align: right;
  font-size: 13px;
  font-weight: 600;
  color: #8d8d8d;
}
.projects-founded {
  font-size: 13px;
  font-weight: 600;
  color: #8d8d8d;
}
.projects-item__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 17px 0;
}
.projects-item__footer {
  text-align: right;
  border-top: 1px solid rgba(0, 0, 0, 0.14);
  line-height: 1;
  padding-top: 24px;
}
.projects-item__footer span {
  margin-left: 10px;
  position: relative;
  top: 2px;
}

.careers_bg {
  background: url(../../img/bg.png) no-repeat;
  padding-bottom: 60px;
}
.careers {
  padding-top: 100px;
}
.careers-heading {
  text-align: center;
}
.careers-heading h2 {
  font-size: 39px;
  color: #99c50a;
  font-weight: 600;
  margin-bottom: 42px;
}
.careers-heading p {
  font-size: 19px;
  font-weight: 400;
  color: #949494;
  margin-bottom: 100px;
}
.careers-reviews {
  display: flex;
  justify-content: space-between;
}
.review__wrap {
  max-width: 398px;
}
.careers-reviews__item {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  padding: 25px 36px;
  position: relative;
}
.careers-reviews__item h4 {
  font-size: 21px;
  font-weight: 600;
  color: #99c50a;
  margin-bottom: 22px;
}
.careers-reviews__item p {
  color: #848484;
  font-size: 13px;
}
.careers-reviews__item:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -22px;
  left: 80px;
  border-top: 22px solid #fff;
  border-right: 22px solid rgba(255, 255, 255, 0);
}
.review__user {
  display: flex;
  align-items: center;
  margin-top: 32px;
  color: #494949;
  font-family: Montserrat;
  font-size: 15px;
  font-weight: 600;
}
.review__img {
  margin-right: 18px;
  margin-left: 50px;
}

.footer {
  padding: 45px 0 25px;
}
.footer-img__list {
  max-width: 1030px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-img__list img {
  margin-right: 26px;
}
.footer-img__list img:last-child {
  margin-right: 0;
}
.footer-copy {
  padding-top: 26px;
  border-top: 1px solid rgba(81, 81, 81, 0.11);
  text-align: center;
  margin-top: 40px;
  font-size: 13px;
  color: #848484;
}

@media (max-width: 1200px) {
  .wrapper {
    padding-right: 30px;
    padding-left: 30px;
  }
  .header {
    padding: 21px 50px;
  }
}
.work-properties__wrap {
  padding: 46px;
}
.work-biers__wrap {
  padding: 46px;
}
@media (max-width: 992px) {
  .wrapper {
    padding-right: 15px;
    padding-left: 15px;
  }
  .header {
    padding: 21px 15px;
  }
  .header__logo img {
    width: 148px;
  }
  .header-nav a {
    font-size: 12px;
    margin-right: 15px;
  }
  .intro h1 {
    line-height: 60px;
    font-size: 36px;
  }
  .intro h1 span {
    font-size: 28px;
  }
  .intro__btn {
    margin-top: 32px;
    padding: 23px 30px 19px;
    font-size: 18px;
  }
  .presentation {
    padding: 70px 0 105px;
  }
  .presentation h2 {
    margin: 35px 0;
    font-size: 28px;
    line-height: 46px;
  }
  .presentation p {
    margin: 35px 0;
    font-size: 17px;
  }
  .work-article {
    padding-bottom: 20px;
  }
  .work-biers__wrap,
  .work-properties__wrap {
    padding: 18px;
  }
  .review__wrap {
    max-width: 350px;
  }
  .careers-heading h2 {
    font-size: 36px;
    margin-bottom: 35px;
  }
  .careers-heading p {
    font-size: 18px;
  }
  .careers-reviews__item {
    padding: 20px 28px;
  }
  .careers-reviews__item h4 {
    font-size: 19px;
    margin-bottom: 12px;
  }
  .careers-reviews__item p {
    font-size: 12px;
  }
  .footer-img__list {
    flex-wrap: wrap;
  }
  .footer-img__list img {
    margin-top: 15px;
  }
}
@media (max-width: 767px) {
  .wrapper {
    padding-right: 5px;
    padding-left: 5px;
  }
  .header-nav {
    display: none;
  }
  .toggle-nav {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 0;
    top: 72px;
    height: 550px;
    width: 200px;
    box-sizing: border-box;
    padding: 20px;
    z-index: 1;
    background: rgba(81, 81, 81, 0.8);
    animation: show 1s;
  }
  .toggle-nav a {
    color: #fff;
    margin: 15px;
  }
  @keyframes show {
    from {
      right: -200px;
    }
    to {
      right: 0;
    }
  }
  .burger__menu {
    display: block;
  }
  .intro {
    height: 550px;
    background-position: center;
  }
  .presentation {
    padding: 40px 0 50px;
  }
  .presentation img {
    width: 40px;
  }
  .presentation h2 {
    margin: 20px 0;
    font-size: 23px;
    line-height: 2;
  }
  .presentation p {
    margin: 20px 0;
    font-size: 15px;
  }
  .work-biers,
  .work-properties {
    flex-direction: column;
  }
  .work-biers__wrap,
  .work-properties__wrap {
    padding: 10px;
  }
  .work-biers__wrap .work__pic,
  .work-properties__wrap .work__pic {
    margin-right: 0;
    margin-bottom: 18px;
  }
  .work-biers__wrap .work-article h3,
  .work-properties__wrap .work-article h3 {
    font-size: 20px;
  }
  .work-biers__wrap .work-article p,
  .work-properties__wrap .work-article p {
    font-size: 14px;
    margin: 20px 0 36px;
  }
  .projects-heading {
    flex-direction: column;
    align-items: flex-start;
  }
  .projects-heading__article {
    width: 100%;
  }
  .projects-heading__btn {
    margin-top: 15px;
  }
  .projects-list {
    flex-direction: column;
    align-items: center;
  }
  .projects-item {
    width: 65%;
    margin-bottom: 30px;
  }
  .careers {
    padding-top: 0;
  }
  .careers-reviews {
    flex-direction: column;
    align-items: center;
  }
  .careers-heading h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  .careers-heading p {
    margin-bottom: 30px;
    font-size: 16px;
  }
  .review__wrap {
    margin-bottom: 20px;
  }
  .careers_bg {
    padding-bottom: 0;
  }
  .footer-img__list img {
    margin-top: 10px;
    margin-right: 12px;
  }
  .footer-img__list {
    padding: 0 5px;
  }
}
@media (max-width: 479px) {
  .intro h1 {
    font-size: 31px;
    line-height: 46px;
  }
  .intro h1 span {
    font-size: 24px;
  }
  .presentation h2 {
    font-size: 16px;
  }
  .presentation p {
    margin: 16px 0;
    font-size: 13px;
  }
  .work {
    flex-direction: column;
  }
  .work .work-biers__wrap,
  .work .work-properties__wrap {
    width: 100%;
    text-align: center;
  }
  .projects-heading {
    text-align: center;
    align-items: center;
  }
  .projects-item {
    width: 98%;
  }
  .footer-img__list {
    padding: 0;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .footer-img__list img {
    margin: 12px 0;
  }
  .work-biers__wrap,
  .work-properties__wrap {
    padding: 10px 0;
  }
}
